@tailwind base;
@tailwind components;
@tailwind utilities;

.btn-indigo {
    @apply text-sm font-medium px-6 py-2 bg-indigo-600 rounded-md hover:bg-indigo-500 hover:shadow-md transition duration-200
}

.btn-gray {
    @apply text-sm font-medium px-6 py-2 bg-grey-675 rounded-md hover:bg-grey-650 hover:shadow-md transition duration-200
}

.border-default {
    @apply border-grey-700
}

.divide-default {
    @apply divide-grey-750
}

.date-label {
    @apply cursor-pointer text-sm px-2 py-1 bg-grey-725 rounded border-1 border-grey-650 hover:bg-grey-700 shrink-0 transition duration-200
}

.padding-td-default {
    @apply py-3 px-5
}

.text-table {
    font-size: 0.95rem; /* 14px */
    line-height: 1.35rem; /* 20px */
}